import React, {useState, useEffect} from 'react';
import style from './Project.module.css';

import ProjectData from '../../dataset/project.json';

import PCView from './ProjectPCView.js';
import MobileView from './ProjectMobileView.js';

function Project(){
    const [viewStep, setViewStep] = useState(0);                // 출력 화면 단계 (0: 1350~ / 1: 830~1350 / 2: 530~830 / 3: ~530)
    const [isPC, setIsPC] = useState(true);                     // PC 화면 출력 여부 (true: PC화면 / false: 모바일 or 태블릿)
    const [projectMenu, setProjectMenu] = useState('all');      // 콘텐츠 메뉴 (all: 전체 / web: 웹 프로젝트 / data: 데이터 프로젝트 / ai: 인공지능 프로젝트)
    const [menuStyle, setMenuStyle] = useState([{'color':'black', 'size':'25px'},   // 콘텐츠 메뉴 스타일(폰트 색, 폰트 크기)
                                                {'color':'gray', 'size':'20px'},
                                                {'color':'gray', 'size':'20px'},
                                                {'color':'gray', 'size':'20px'}]);
    const [webScroll, setWebScroll] = useState(0);      // 웹 프로젝트 스크롤 퍼센트
    const [dataScroll, setDataScroll] = useState(0);    // 데이터 프로젝트 스크롤 퍼센트
    const [aiScroll, setAiScroll] = useState(0);        // 인공지능 프로젝트 스크롤 퍼센트
    const [allScroll, setAllScroll] = useState(0);      // 전체 프로젝트 스크롤 퍼센트
    const [contentDisplay, setContentDisplay] = useState('none');   // 콘텐츠 출력 여부(none: 출력x / block: 출력)


    // 프로젝트 페이지 데이터(from json) - 순서대로 웹, 데이터, 인공지능, 전체
    const webProject = ProjectData.Web.map((item, idx) => {
        return(
            <div className={style.per} style={{animationDelay:'0.3s'}}>
                {item.title}
            </div>
        );
    });
    const dataProject = ProjectData.Data.map((item, idx) => {
        return(
            <div className={style.per} style={{animationDelay:'0.3s'}}>
                {item.title}
            </div>
        );
    })
    const aiProject = ProjectData.AI.map((item, idx) => {
        return(
            <div className={style.per} style={{animationDelay:'0.3s'}}>
                {item.title}
            </div>
        );
    })
    const allProject = webProject.concat(dataProject, aiProject);

    // 콘텐츠 스크롤 이벤트 - 순서대로 웹, 데이터, 인공지능, 전체
    const webScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setWebScroll(percentage);
    }
    const dataScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setDataScroll(percentage);
    }
    const aiScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setAiScroll(percentage);
    }
    const allScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setAllScroll(percentage);
    }

    // 콘텐츠 메뉴 스타일
     function changeMenu(_menu){
        setProjectMenu(_menu);
        const selectedColor = 'black';
        const unSelectedColor = 'gray';
        const selectedSize = viewStep === 3 ? '18px' : viewStep === 2 ? '22px' : '25px';
        const unSelectedSize = viewStep === 3 ? '13px' : viewStep === 2 ? '17px' : '20px';
        if(_menu === 'all'){ setMenuStyle([{'color':selectedColor, 'size':selectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}]); }
        else if(_menu === 'web'){ setMenuStyle([{'color':unSelectedColor, 'size':unSelectedSize}, {'color':selectedColor, 'size':selectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}]); }
        else if(_menu === 'data'){ setMenuStyle([{'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':selectedColor, 'size':selectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}]); }
        else if(_menu === 'ai'){ setMenuStyle([{'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':unSelectedColor, 'size':unSelectedSize}, {'color':selectedColor, 'size':selectedSize}]); }
    }

    // 콘텐츠 표출 딜레이 - 타이틀 애니메이션 끝난 후
    useEffect(() => {
        const timer = setTimeout(() => {
            setContentDisplay('grid');
        }, 4000);
        return () => clearTimeout(timer);
    }, []);

    // 윈도우 크기 변경 이벤트 - 화면 크기에 맞추어 출력 화면 단계 변경
    const windowResizeEvent = () => {
        const windowWidth = window.innerWidth;
        if(windowWidth>1350){ setViewStep(0);}
        else if(830<windowWidth && windowWidth<=1350){ setViewStep(1);}
        else if(550<windowWidth && windowWidth<=830){ setViewStep(2);}
        else if(windowWidth<=530){ setViewStep(3);}
    }
    useEffect(() => {
        windowResizeEvent();
        if(viewStep === 0){ setIsPC(true); }
        else if(viewStep === 1){ setIsPC(false); changeMenu(projectMenu); }
        else if(viewStep === 2){ setIsPC(false); changeMenu(projectMenu); }
        else if(viewStep === 3){ setIsPC(false); changeMenu(projectMenu); }
    }, []);
    useEffect(() => {
        if(viewStep === 0){ setIsPC(true); }
        else if(viewStep === 1){ setIsPC(false); changeMenu(projectMenu); }
        else if(viewStep === 2){ setIsPC(false); changeMenu(projectMenu); }
        else if(viewStep === 3){ setIsPC(false); changeMenu(projectMenu); }
    }, [viewStep]);
    useEffect(() => {
        window.addEventListener('resize', windowResizeEvent);
        return() => { window.removeEventListener('resize', windowResizeEvent); }
    }, []);

    // 이건 나중에 할 거 -> pc화면, 태블릿 and 모바일 화면을 파일 자체를 분리해서 import 후에 초기 출력 화면 단계에 따라 const값 변경하기.
    // 초기 윈도우 크기 확인 - 출력 화면 단계 초기 설정
    // useEffect(() => {
    //     const windowWidth = window.innerWidth;
    //     if(windowWidth>1350){ setViewStep(0);}
    //     else if(830<windowWidth && windowWidth<=1350){ setViewStep(1);}
    //     else if(550<windowWidth && windowWidth<=830){ setViewStep(2);}
    //     else if(windowWidth<=530){ setViewStep(3);}
    //     console.log(windowWidth);
    // }, []);


    return(
        <div>
            {/* 페이지  타이틀 */}
            <div className={style.page_title}>
                <svg height="100" stroke="#000000" strokeWidth="3" className={style.text_line} width="100%">
                    <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" >
                        Project
                    </text>
                </svg>
            </div>

            <div className={style.wrapper} style={{display:contentDisplay}}>
                { isPC
                    ?
                        <PCView/>
                    :
                        <MobileView viewStep={viewStep}/>
                }
            </div>
        </div>
    );
}

export default Project;