import React, {useState, useEffect} from 'react';
import style from './Skill.module.css';

import SkillData from '../../dataset/skill.json';

import noimg from './no.png';

function Skill(){
    const [contentDisplay, setContentDisplay] = useState('none');   // 콘텐츠 출력 여부(none: 출력x / block: 출력)
    const [skillScroll, setSkillScroll] = useState(0);              // 스킬 스크롤 퍼센트
    const [certiScroll, setCertiScroll] = useState(0);              // 자격증 스크롤 퍼센트
    const [infoType, setInfoType] = useState({type:'none', name:''});       // 정보 표시 창의 출력 종류 (none: 빈 창 / skill: 스킬 / cert: 자격증)
    const [info, setInfo] = useState(<div>click icon</div>);                // 정보 표시 창의 출력할 내용


    // 스킬 페이지 데이터(from json) - 순서대로 스킬, 자격증
    const skills = SkillData.Skill.map((item, idx) => {
        const name = item.name;
        return(
            <div key={idx} className={style.per_skill} style={{animationDelay:'0.3s'}} onClick={() => changeInfoType('skill', item.name)}>
                {
                    item.path !== 'no image'
                    ? <svg role="img" viewBox="-3 -3 30 30" xmlns="http://www.w3.org/2000/svg"><path d={item.path}/></svg>
                    : <img src={noimg} width='80%' height='80%' style={{margin:'10%'}}/>
                }
                <div className={style.skillname}><div>{item.name}</div></div>
            </div>
        );
    });
    const certificates = SkillData.Certificate.map((item, idx) => {
        return(
            <div key={idx} className={style.per_cert} style={{animationDelay:'0.3s'}} onClick={() => changeInfoType('cert', item.name)}>
                <div>{item.name}</div>
            </div>
        );
    });

    // 콘텐츠 스크롤 이벤트 - 순서대로 스킬, 자격증
    const skillScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setSkillScroll(percentage);
    }
    const certiScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div 현재 스크롤 높이
        const height = e.target.scrollHeight - e.target.clientHeight;     // div 전체 스크롤 길이(부모 div의 height 뺀거(300))
        const percentage = (top / height) * e.target.clientWidth;      // 스크롤 퍼센트
        setCertiScroll(percentage);
    }

    // 정보 표시 창 타입 변경
    function changeInfoType(_type, _name){ setInfoType({type:_type, name:_name}); }
    useEffect(() => {
        const type = infoType.type;
        const name = infoType.name;
        if(type === 'skill'){
            const info_tmp = SkillData.Skill.filter((item) => item.name===name)[0];
            const info_content =    <div className={style.info_skill}>
                                        <div>{info_tmp.name}</div>
                                        <div>
                                            {
                                                info_tmp.path !== 'no image'
                                                ? <svg role="img" viewBox="-3 -3 30 30" xmlns="http://www.w3.org/2000/svg"><title>Python</title><path d={info_tmp.path}/></svg>
                                                : <img src={noimg} width='80%' height='80%' style={{margin:'10%'}}/>
                                            }
                                        </div>
                                        <div>{info_tmp.explain}</div>
                                    </div>;
            setInfo(info_content);
        }
        else if(type === 'cert'){
            const info_tmp = SkillData.Certificate.filter((item) => item.name===name)[0];
            const info_content =    <div className={style.info_cert}>
                                        <div>{info_tmp.fullname}</div>
                                        <div><span>취득일자</span> : {info_tmp.date} <br/> <span>등급</span> : {info_tmp.level}</div>
                                        <div>{info_tmp.explain}</div>
                                    </div>;
            setInfo(info_content);
        }
        else{
            setInfo(<div>click icon</div>);
        }
        
    }, [infoType]);

    // 콘텐츠 표출 딜레이 - 타이틀 애니메이션 끝난 후로
    useEffect(() => {
        const timer = setTimeout(() => {
            setContentDisplay('grid');
        }, 4000);
        return () => clearTimeout(timer);
    }, []);


    return(
        <div>
            {/* 페이지  타이틀 */}
            <div className={style.page_title}>
                <svg height="100" stroke="#000000" strokeWidth="3" className={style.text_line} width="100%">
                    <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" >
                        Skill
                    </text>
                </svg>
            </div>


            <div className={style.wrapper} style={{display:contentDisplay}}>
                {/* 카테고리 */}
                <div className={style.menu}>SKILL</div>
                <div className={style.menu}>CERTIFICATE</div>
                {/* 카테고리 별 스크롤 */}
                <div className={style.scroll}> <span style={{width:skillScroll}}></span> </div>
                <div className={style.scroll}> <span style={{width:certiScroll}}></span> </div>

                {/* 언어 및 프로그램 */}
                <div className={`${style.content} ${style.skill}`} onScroll={skillScrollEvent}>
                    {skills}
                </div>
                
                {/* 자격증 */}
                <div className={`${style.content} ${style.certificate}`} onScroll={certiScrollEvent}>
                    {certificates}
                </div>

                {/* 정보 표시 창 */}
                <div className={`${style.content} ${style.info}`}>
                    {info}
                </div>
            </div>
        </div>
    );
}

export default Skill;