import React, { useState, useEffect } from 'react';
import style from './Profile.module.css';

import Face from './face.jpg';

function Profile(){
    const [contentDisplay, setContentDisplay] = useState('none');   // 콘텐츠 출력 여부(none: 출력x / block: 출력)

    // 콘텐츠 표출 딜레이 - 타이틀 애니메이션 끝난 후
    useEffect(() => {
        const timer = setTimeout(() => {
            setContentDisplay('grid');
        }, 4000);
        return () => clearTimeout(timer);
    }, []);


    return(
        <div>
            {/* 페이지  타이틀 */}
            <div className={style.page_title}>
                <svg height="100" stroke="#000000" strokeWidth="3" className={style.text_line} width="100%">
                    <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" >
                        Profile
                    </text>
                </svg>
            </div>

            <div className={style.wrapper} style={{display:contentDisplay}}>
                <div className={style.me}>
                    <img id={style['photo']} src={Face}/>

                    <div id={style['intro']}>
                        <p><b>남기범</b> / Data Engineer</p>
                        <div>"데이터 파이프라인을 구축하고, 분석 기술과 AI를 이용하여 새로운 가치를 창출하는 엔지니어 남기범입니다."</div>
                    </div>
                </div>

                <div className={style.exp}>
                    <div>
                        <div>프로그램 설계자</div>
                        세미나와 코딩 아카데미를 참여하여 프로그램에 대한 기본적인 지식을 공부하였고, 프로젝트를 진행하면서 프로그램을 설계하는 법을 익혔습니다.
                    </div>

                    <div>
                        <div>한 명의 연구자</div>
                        1년 동안 학부 연구생으로 일을 하며 아이디어 회의 및 개발 작업을 진행하였으며, YOLO와 LSTM등의 인공지능 모델을 사용하여 실제 프로젝트를 진행한 경험이 있습니다.
                    </div>

                    <div>
                        <div>성장하는 인재</div>
                        지속적으로 세미나에 참여하여 최근 개발 지식을 습득하고, 개인 프로젝트 수행으로 지속적으로 성장하는 엔지니어가 되겠습니다.
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Profile;